'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { ArrowLeft, Search, UserPlus, Loader2 } from 'lucide-react';
import Link from 'next/link';
import { useAuth } from '@/contexts/AuthContext';
import { useFriend } from '@/hooks/useFriend';
import { searchUsers, getRecommendedUsers } from '@/lib/api'; // âœ… Import API functions

interface SearchUser {
    _id: string;
    fullName: string;
    email: string;
    avatar?: string;
    username?: string;
    language?: string;
    country?: string;
}

export default function AddFriendsPage() {
    const router = useRouter();
    const { user } = useAuth();
    const { friends, sendFriendRequest } = useFriend();
    
    const [searchQuery, setSearchQuery] = useState('');
    const [searchResults, setSearchResults] = useState<SearchUser[]>([]);
    const [recommendedUsers, setRecommendedUsers] = useState<SearchUser[]>([]);
    const [loading, setLoading] = useState(false);
    const [initialLoading, setInitialLoading] = useState(true);
    const [sendingRequests, setSendingRequests] = useState<Set<string>>(new Set());

    // âœ… Load recommended users on mount (giá»¯ nguyÃªn logic cÅ©)
    useEffect(() => {
        const loadRecommendedUsers = async () => {
            try {
                setInitialLoading(true);
                console.log('ðŸ” Loading recommended users...');
                
                const data = await getRecommendedUsers(); // âœ… DÃ¹ng API function
                
                const filteredUsers = (data.users || []).filter((u: SearchUser) =>
                    u._id !== user?._id &&
                    !friends.some(f => f._id === u._id)
                );
                
                setRecommendedUsers(filteredUsers);
                console.log('âœ… Recommended users loaded:', filteredUsers.length);
                
            } catch (error) {
                console.error('âŒ Failed to load recommended users:', error);
            } finally {
                setInitialLoading(false);
            }
        };

        if (user?._id) {
            loadRecommendedUsers();
        }
    }, [user?._id, friends]);

    // âœ… Search function (giá»¯ nguyÃªn logic, chá»‰ thay API call)
    const handleSearch = async () => {
        if (!searchQuery.trim()) {
            setSearchResults([]);
            return;
        }

        setLoading(true);
        try {
            const data = await searchUsers(searchQuery); // âœ… DÃ¹ng API function
            
            // âœ… Giá»¯ nguyÃªn filtering logic
            const filteredUsers = (data.users || []).filter((u: SearchUser) =>
                u._id !== user?._id &&
                !friends.some(f => f._id === u._id)
            );
            setSearchResults(filteredUsers);
            
        } catch (error: any) {
            console.error("âŒ Search request failed:", error);
            
            // âœ… Giá»¯ nguyÃªn error handling
            if (error.message.includes('401') || error.message.includes('Unauthorized')) {
                console.log('ðŸ”„ Unauthorized - clearing storage and redirecting');
                localStorage.removeItem('auth_token');
                localStorage.removeItem('auth_user');
                router.push("/sign-in");
            }
        } finally {
            setLoading(false);
        }
    };

    // âœ… Handle search submit
    const handleSearchSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        await handleSearch();
    };

    // âœ… Handle send friend request
    const handleSendRequest = async (userId: string) => {
        try {
            setSendingRequests(prev => new Set(prev).add(userId));
            const success = await sendFriendRequest(userId);
            
            if (success) {
                console.log('âœ… Friend request sent successfully');
                // Remove user from both lists
                setSearchResults(prev => prev.filter(u => u._id !== userId));
                setRecommendedUsers(prev => prev.filter(u => u._id !== userId));
            }
        } catch (error) {
            console.error('âŒ Error sending friend request:', error);
        } finally {
            setSendingRequests(prev => {
                const newSet = new Set(prev);
                newSet.delete(userId);
                return newSet;
            });
        }
    };

    // âœ… Determine which users to show
    const usersToShow = searchQuery.trim() ? searchResults : recommendedUsers;
    const isSearchMode = searchQuery.trim().length > 0;

    return (
        <div className="min-h-screen bg-gray-900 text-white">
            <div className="container mx-auto px-4 py-6">
                {/* Header */}
                <div className="flex items-center gap-4 mb-6">
                    <Link 
                        href="/friends" 
                        className="flex items-center gap-2 text-gray-400 hover:text-white transition-colors"
                    >
                        <ArrowLeft className="w-5 h-5" />
                        Back to Friends
                    </Link>
                </div>

                <div className="text-center mb-8">
                    <h1 className="text-3xl font-bold mb-2">Add Friends</h1>
                    <p className="text-gray-400">Connect with language learners around the world</p>
                </div>

                {/* Search Form */}
                <div className="max-w-2xl mx-auto mb-8">
                    <form onSubmit={handleSearchSubmit} className="relative">
                        <div className="flex items-center gap-2">
                            <div className="relative flex-1">
                                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                                <input
                                    type="text"
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                    placeholder="Search by username or email"
                                    className="w-full bg-gray-800 border border-gray-700 rounded-lg pl-10 pr-4 py-3 text-white placeholder-gray-400 focus:border-blue-500 focus:outline-none"
                                />
                            </div>
                            <button
                                type="submit"
                                disabled={loading}
                                className="bg-blue-600 hover:bg-blue-700 disabled:bg-gray-600 px-6 py-3 rounded-lg font-medium transition-colors flex items-center gap-2"
                            >
                                {loading ? (
                                    <Loader2 className="w-4 h-4 animate-spin" />
                                ) : (
                                    <Search className="w-4 h-4" />
                                )}
                                Search
                            </button>
                        </div>
                    </form>
                </div>

                {/* Results Section */}
                <div className="max-w-4xl mx-auto">
                    {initialLoading ? (
                        <div className="text-center py-12">
                            <Loader2 className="w-8 h-8 animate-spin mx-auto mb-4 text-blue-500" />
                            <p className="text-gray-400">Loading users...</p>
                        </div>
                    ) : usersToShow.length > 0 ? (
                        <div>
                            <h2 className="text-xl font-semibold mb-4">
                                {isSearchMode ? `Search Results (${usersToShow.length})` : `Recommended Users (${usersToShow.length})`}
                            </h2>
                            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                                {usersToShow.map((user) => (
                                    <UserCard
                                        key={user._id}
                                        user={user}
                                        onSendRequest={handleSendRequest}
                                        isSending={sendingRequests.has(user._id)}
                                    />
                                ))}
                            </div>
                        </div>
                    ) : (
                        <div className="text-center py-12">
                            <div className="w-16 h-16 bg-gray-800 rounded-full flex items-center justify-center mx-auto mb-4">
                                <Search className="w-8 h-8 text-gray-400" />
                            </div>
                            <h3 className="text-xl font-semibold mb-2">
                                {isSearchMode ? 'No users found' : 'No users available'}
                            </h3>
                            <p className="text-gray-400">
                                {isSearchMode 
                                    ? 'Try adjusting your search terms'
                                    : 'No recommended users at the moment'
                                }
                            </p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}

// âœ… UserCard component (giá»¯ nguyÃªn)
function UserCard({ 
    user, 
    onSendRequest, 
    isSending 
}: { 
    user: SearchUser; 
    onSendRequest: (userId: string) => void;
    isSending: boolean;
}) {
    return (
        <div className="bg-gray-800 rounded-lg p-4 border border-gray-700 hover:border-gray-600 transition-colors">
            <div className="flex items-start gap-3">
                <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white font-semibold">
                    {user.fullName.charAt(0).toUpperCase()}
                </div>
                <div className="flex-1 min-w-0">
                    <h3 className="font-semibold text-white truncate">{user.fullName}</h3>
                    {user.username && (
                        <p className="text-sm text-gray-400 truncate">@{user.username}</p>
                    )}
                    <p className="text-sm text-gray-400 truncate">{user.email}</p>
                    {(user.language || user.country) && (
                        <div className="flex items-center gap-2 mt-1">
                            {user.language && (
                                <span className="text-xs bg-gray-700 px-2 py-1 rounded">
                                    {user.language}
                                </span>
                            )}
                            {user.country && (
                                <span className="text-xs bg-gray-700 px-2 py-1 rounded">
                                    {user.country}
                                </span>
                            )}
                        </div>
                    )}
                </div>
            </div>
            <button
                onClick={() => onSendRequest(user._id)}
                disabled={isSending}
                className="w-full mt-3 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-600 text-white py-2 rounded-lg font-medium transition-colors flex items-center justify-center gap-2"
            >
                {isSending ? (
                    <Loader2 className="w-4 h-4 animate-spin" />
                ) : (
                    <UserPlus className="w-4 h-4" />
                )}
                {isSending ? 'Sending...' : 'Add Friend'}
            </button>
        </div>
    );
}